\n}\r\n\r\n/*\r\n   Like\r\n*/\r\n.like {\r\n  font-size: 18px;\r\n  position: fixed;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  background-color: #999;\r\n  color: white;\r\n  border: 0;\r\n  border-radius: 50%;\r\n  width: 55px;\r\n  height: 55px;\r\n  cursor: pointer;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}\r\n\r\n/* Review */\r\n.review {\r\n  margin: 0;\r\n  padding: 23px 0;\r\n  background-color: #999;\r\n}\r\n\r\n.review > h2,\r\n.review_item {\r\n  margin: .5rem;\r\n  padding: .3rem;\r\n  font-size: 0.7rem;\r\n}\r\n\r\n.review > small {\r\nmargin-top: 3px;\r\nfont-size: 14px;\r\ncolor: gray;\r\n}\r\n\r\n.review_item {\r\n  background: right/contain content-box border-box no-repeat white;\r\n}\r\n\r\n.review_item > small,\r\n.review_item > p {\r\n  margin: .2rem;\r\n  font-size: 1rem;\r\n}\r\n\r\n.content {\r\n    padding: 10px;\r\n}\r\n\r\n  footer {\r\n    background-color: #999;\r\n    padding: 20px;\r\n    width: 100%;\r\n    text-align: center;\r\n  }\r\n   \r\n  footer a {\r\n    padding: 13.5px;\r\n    text-decoration: none;\r\n    color: #616161;\r\n  \r\n  }\r\n\r\n\r\n  @media screen and (max-width:800px){\r\n    #hamburger {\r\n      display: block;\r\n    }\r\n\r\n    #drawer {\r\n      width: 250px;\r\n      height: 100%;\r\n      position: absolute;\r\n      transform: translate(-250px, 0);\r\n      transition: transform 0.3s ease-in-out;\r\n      background-color:#999;\r\n    }\r\n    \r\n    #drawer.open {\r\n      transform: translate(0,0)\r\n    }\r\n\r\n    #drawer ul {\r\n      list-style-type: none;\r\n      padding: 0 13px;\r\n      line-height: 70px;\r\n      text-align: center;\r\n    }\r\n     \r\n    #drawer li {\r\n      padding: 5px 0px;\r\n    }\r\n     \r\n    #drawer a {\r\n      text-decoration: none ;\r\n      color: #616161;\r\n      font-size: 15px;\r\n      padding: 14px;\r\n      text-align: center;\r\n    }\r\n     \r\n    #hamburger {\r\n      font-size: 21.5px;\r\n      text-decoration: none;\r\n      color: #616161;\r\n    }\r\n\r\n    .restaurants {\r\n      grid-template-columns: 1fr 1fr;\r\n    }\r\n  \r\n    .restaurants {\r\n      grid-template-columns: auto 1fr;\r\n    }\r\n  \r\n    .restaurants .restaurants__name {\r\n      grid-column-start: 1;\r\n      grid-column-end: 3;\r\n    }\r\n  \r\n    .restaurants .restaurants__overview {\r\n      grid-column-start: 1;\r\n      grid-column-end: 3;\r\n    }\r\n  }\r\n  \r\n    @media screen and (min-width: 800px) {\r\n      .restaurants {\r\n        grid-template-columns: repeat(3, 1fr);\r\n        padding: 18px 8px;\r\n      }\r\n    }\r\n  \r\n    @media screen and (min-width: 900px) {\r\n      .restaurants {\r\n        grid-template-columns: repeat(4, 1fr);\r\n      }\r\n    }\r\n    \r\n  \r\n    @media screen and (min-width: 1200px) {\r\n      .restaurants {\r\n        grid-template-columns: repeat(5, 1fr);\r\n      }\r\n    }\r\n  "],"sourceRoot":""}]);
